import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ProjectsList } from '../tc/tc.component';

@Injectable({
  providedIn: 'root'
})
export class IcservicesService {

  constructor(private httpClient: HttpClient) {}

  getProjectsLists() {
    return this.httpClient.get<ProjectsList[]>(`http://10.0.0.7:1400/ic/api/getprojectslist`);
  }

  getClickedProjects(projectname: string) {
    return this.httpClient.get(`http://10.0.0.7:1400/ic/api/getlabelslist?project_name=${projectname}`);
  }

  getImageLists(projectname: string, labelname: string){
    return this.httpClient.get(`http://10.0.0.7:1400/ic/api/getimageslist?project_name=${projectname}&label_name=${labelname}`);
  }

  addNewProjects(projectname: any){
    return this.httpClient.get(`http://10.0.0.7:1400/ic/api/addproject?project_name=${projectname}`);
  }

  editProjects(oldprojectname: any, newprojectname: any){
    return this.httpClient.get(`http://10.0.0.7:1400/ic/api/renameproject?project_name=${oldprojectname}&rename_project=${newprojectname}`);
  }

  deleteProjects(projectname: string){
    return this.httpClient.delete(`http://10.0.0.7:1400/ic/api/deleteproject?project_name=${projectname}`);
  }

  addNewLabels(newlabelname: any, projectname:any){
    return this.httpClient.get(`http://10.0.0.7:1400/ic/api/addlabel?label_name=${newlabelname}&project_name=${projectname}`)
  }

  editLabels(projectname: any, newlabelname: any, oldlabelname: any){
    console.log(projectname, newlabelname, oldlabelname)
    // tslint:disable-next-line: max-line-length
    return this.httpClient.get(`http://10.0.0.7:1400/ic/api/renamelabels?project_name=${projectname}&rename_label=${newlabelname}&label_name=${oldlabelname}`);
  }

  deleteLabels(projectname: any, labelname: any){
    return this.httpClient.delete(`http://10.0.0.7:1400/ic/api/deletelabels?project_name=${projectname}&label_delete=${labelname}`);
  }

  getPickleLists(projectname: any){
    return this.httpClient.get(`http://10.0.0.7:1400/ic/api/getmodelslist?project_name=${projectname}`);
  }

  getPredictDatas(projectname: any, fetchedmodel: any, givenurl: any){
    return this.httpClient.post('http://10.0.0.7:1400/ic/api/predict', {"project_name": projectname, "model_name": fetchedmodel, "url": givenurl })
  }

  buildimage(projectname: any, tbatch: any, vbatch: any, epochs: any){
    return this.httpClient.post('http://10.0.0.7:1400/ic/api/trainmodel', { "project_name": projectname, "batch_size": tbatch, "validation_size": vbatch, "epochs": epochs })
  }

  deployProjects(deploy: any, modelname: any, notid: any, project_name:any){
    return this.httpClient.post('http://10.0.0.7:1400/ic/api/deployment', { "deploy_o": deploy, "model_name": modelname, "not_id": notid,"project_name": project_name });
  }

  uploadZipFiles(formData: any){
    return this.httpClient.post('http://10.0.0.7:1400/ic/api/addzipfile', formData);
  }

  uploadImages(formData: any){
    return this.httpClient.post('http://10.0.0.7:1400/ic/api/addimage', formData);
  }

  uploadPredictImages(formData: any){
    return this.httpClient.post('http://capture.eaiesb.com', formData);
  }

  deleteimages(projectname: any, labelname: any, deleteimage: any){
    return this.httpClient.delete(`http://10.0.0.7:1400/ic/api/deleteimage?project_name=${projectname}&label_name=${labelname}&image_delete=${deleteimage}`)
  }


  predictinfo(projectName: any, model: any, url: any, result: any, newLabelName: any, product: any, imagename: any) {
    return this.httpClient.post('https://server3.eaiesb.com:8443/predict/api/mongo/predict/insertpredict', [{ "projectName": projectName, "model": model, "url": url, "result": result, "newLabelName": newLabelName, "product": "IC", "imagename": imagename }]);
  }


    rebuildinfo(projectname:any,labelname:any,imagename:any){
      return this.httpClient.get(`http://10.0.0.7:1400/ic/api/getnewimagelabel?project_name=${projectname}&label_name=${labelname}&image_name=${imagename}`)
    }

  getAllPredictList(projectname: any) {
    return this.httpClient.get(`https://server3.eaiesb.com:8443/predict/api/mongo/predict/getpredictdata/${projectname}`);
  }

  getImgUrl(formData : any){
    return this.httpClient.post('http://10.0.0.7:1400/ic/api/uploadimage',formData);
  }

}
