  // zip image file upload service
  public uploaderZip: FileUploader = new FileUploader({});

  uploadZipFile() {
    let files = this.getFiles1();
    let requests = [];
    files.forEach(file => {
      let formData = new FormData();
      formData.append("file", file.rawFile, file.name);
      this.icservice.uploadZipFiles(formData).subscribe(response => { });
      this.getImageList(this.savedProjectName, this.savedLabelName);
      this.getClickedProject(this.savedProjectName);
    }, error => {
      alert(error.error.message);
      console.log(error.error.message);
    });
  }

  getFiles1(): FileLikeObject[] {
    return this.uploaderZip.queue.map(fileItem => {
      return fileItem.file;
    });
  }
  // zip image file upload service

  // multi image upload service
  public uploaderImage: FileUploader = new FileUploader({});

  uploadImage() {
    let files = this.getFiles2();
    let requests = [];
    files.forEach(file => {
      let formData = new FormData();
      formData.append("project_name", this.savedProjectName);
      formData.append("label_name", this.savedLabelName);
      formData.append("file", file.rawFile, file.name);
      this.icservice.uploadImages(formData).subscribe(response => { });
      this.getImageList(this.savedProjectName, this.savedLabelName);
      this.getClickedProject(this.savedProjectName);
    }, error => {
      alert(error.error.message);
      console.log(error.error.message);
    });
  }

  getFiles2(): FileLikeObject[] {
    return this.uploaderImage.queue.map(fileItem => {
      return fileItem.file;
    });
  }
  // end of multi image file upload


