import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ProjectsList, EditProject, DeleteProject, CSVList, ModelList } from '../tc/tc.component';

@Injectable({
  providedIn: 'root'
})
export class TcservicesService {

  constructor(private httpClient: HttpClient) { }
  getProjectsList() {
    console.log('project');
    return this.httpClient.get<ProjectsList[]>(`http://10.0.0.7:1300/tc/api/getprojectslist`);
  }

  addNewProjects(newProjectData: string){
    return this.httpClient.post('http://10.0.0.7:1300/tc/api/addproject',{"project_name" : newProjectData});
  }

  editProjects(projName: string, renameProj: string){
    return this.httpClient.get<EditProject[]>(`http://10.0.0.7:1300/tc/api/renameproject?project_name=${projName}&rename_project=${renameProj}`);
  }

  deleteProjects(projName: string){
    return this.httpClient.delete<DeleteProject[]>(`http://10.0.0.7:1300/tc/api/deleteproject?project_name=${projName}`);
  }

  getCsvLists(projName: string){
    return this.httpClient.get<CSVList[]>(`http://10.0.0.7:1300/tc/api/getcsvlist?project_name=${projName}`);
  }


  buildProjects(projName: string, CvsName: string, mcwords, tbatch, epochs, maxLen){
    console.log(projName, CvsName, mcwords, tbatch, epochs, maxLen);
    return this.httpClient.post('http://10.0.0.7:1300/tc/api/trainmodel', {
      "project_name": projName,
      "csv_file": CvsName,
      "most_common_words": mcwords,
      "batch_size": tbatch,
      "epochs": epochs,
      "maximum_length": maxLen
  });
  }

  getPickleLists(projName: string){
    return this.httpClient.get<ModelList>(`http://10.0.0.7:1300/tc/api/getmodelslist?project_name=${projName}`);

  }

  getPredictDatas(input: string, projName: string, pickleName: string){
    return this.httpClient.post('http://10.0.0.7:1300/tc/api/predict', {
      "INPUT": input,
      "project_name": projName,
      "file_name": pickleName
    });
  }

  deployProjects(deploy: any, modelname: any, notid: any, project_name:any){
    console.log(modelname, deploy, notid, project_name)
    return this.httpClient.post('http://10.0.0.7:1300/tc/api/deployment', { "deploy_o": deploy, "model_name": modelname, "not_id": notid,"project_name": project_name });
  }

  // deployProjects(projName: string, modelName: string, deployObj: string, notifID: string){
  //   return this.httpClient.post('http://10.0.0.7:1300/tc/api/deployment', {
  //     "project_name": projName,
  //     "model_name": modelName,
  //     "deploy_o": deployObj,
  //     "not_id": notifID
  //   });
  }

