import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { projectlistall, labellistall, imageslistall, labelledlistall, unlabelledlistall, addLabel, addlabelimages, picklelist, labelledimages, unlabelledimages, addProjects } from '../mlic/mlic.component';

@Injectable({
  providedIn: 'root'
})
export class MlicservicesService {

  constructor(private http: HttpClient) { }
  retrieveProjectsList() {
    return this.http.get<projectlistall[]>(
      "http://10.0.0.7:1200/mlic/api/getprojectslist"
    );
  }
  retrieveLabelList(projectname: any) {
    return this.http.get<labellistall[]>(
      `http://10.0.0.7:1200/mlic/api/getlabelslist?project_name=${projectname}`
    );
  }

  retrieveimageList(projectname: any) {
    return this.http.get<imageslistall[]>(
      `http://10.0.0.7:1200/mlic/api/allimageslist?project_name=${projectname}`
    );
  }

  retrievelabelledImagesList(projectname: any) {
    return this.http.get<labelledlistall[]>(
      `http://10.0.0.7:1200/mlic/api/getlabelimageslist?project_name=${projectname}`
    );
  }

  retrieveunlabelledImageList(projectname: any) {
    return this.http.get<unlabelledlistall[]>(
      `http://10.0.0.7:1200/mlic/api/getunlabelledlist?project_name=${projectname}`
    );
  }

  retrieveimagesbylabel(project_name: any, label_name: any) {
    return this.http.get<imageslistall[]>(
      `http://10.0.0.7:1200/mlic/api/getimagesbylabel?project_name=${project_name}&label_name=${label_name}`
    );
  }

  addproject(projectname: any) {
    return this.http.get<addProjects[]>(
      `http://10.0.0.7:1200/mlic/api/addproject?project_name=${projectname}`
    );
  }

  deleteproject(projectname: any) {
    return this.http.delete(
      `http://10.0.0.7:1200/mlic/api/deleteproject?project_name=${projectname}`
    );
  }

  deletelabel(projectname: any, labelname: any) {
    return this.http.delete(
      `http://10.0.0.7:1200/mlic/api/deletelabels?project_name=${projectname}&label_delete=${labelname}`
    );
  }

  addLabel(label_name: string, project_name: string) {
    return this.http.get<addLabel[]>(
      `http://10.0.0.7:1200/mlic/api/addlabel?label_name=${label_name}&project_name=${project_name}`
    );
  }

  addimages(formData) {
    return this.http.post("http://10.0.0.7:1200/mlic/api/addimage", formData);
  }

  addlabeltoimage(project_name: any, imagelist: any, labellist: any) {
    return this.http.get<addlabelimages[]>(
      `http://10.0.0.7:1200/mlic/api/labellingimage?project_name=${project_name}&imagelist=${imagelist}&labellist=${labellist}`
    );
  }

  //buildprojects

  buildProjects(projectname, epochs, tbatch, vbatch) {
    console.log(projectname, epochs, tbatch, vbatch)
    return this.http.post("http://10.0.0.7:1200/mlic/api/trainmodel", {
      "project_name": projectname,
      "batch_size": tbatch,
      "epochs": epochs,
      "validation_size": vbatch
    });
  }

  //prediction

  predictdata(model_name, url, project_name) {
    return this.http.post("http://10.0.0.7:1200/mlic/api/predict", {
      "model_name": model_name,
      "url": url,
      "project_name": project_name
    });
  }

  getmodellist(project_name) {
    return this.http.get<picklelist[]>(
      `http://10.0.0.7:1200/mlic/api/getmodelslist?project_name=${project_name}`
    );
  }

  deployProject(model_name, deploy_o, not_id, project_name) {
    console.log(model_name, deploy_o, not_id, project_name)
    return this.http.post("http://10.0.0.7:1200/mlic/api/deployment", {
      "model_name": model_name,
      "deploy_o": deploy_o,
      "not_id": not_id,
      "project_name": project_name
    });
  }

  //labelled images

  labelledimages(project_name){
    return this.http.get<labelledimages[]>(`http://10.0.0.7:1200/mlic/api/labelledimages?project_name=${project_name}`)
  }

  // unlabelledimages

  unlabelledimages(project_name){
    return this.http.get<unlabelledimages[]>(`http://10.0.0.7:1200/mlic/api/unlabelledimages?project_name=${project_name}`)
  }
//delete images

deleteimage(image_delete,project_name){
  return this.http.delete(`http://10.0.0.7:1200/mlic/api/deleteimage?image_delete=${image_delete}&project_name=${project_name}`)
}


//
}
