import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProductlistComponent } from './productlist/productlist.component';
import { LoginComponent } from './login/login.component';
import { RouteguardService } from './services/routeguard.service';
import { MlicComponent } from './mlic/mlic.component';
import { TcComponent } from './tc/tc.component';
import { ICComponent } from './ic/ic.component';



const routes: Routes = [
  { path: '' , component: LoginComponent },
  { path: 'productlist', component: ProductlistComponent, canActivate: [RouteguardService]},
  { path: 'MLIC', component: MlicComponent, canActivate: [RouteguardService]},
  { path: 'TC', component: TcComponent, canActivate: [RouteguardService]},
  { path: 'IC', component: ICComponent, canActivate: [RouteguardService]}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
